.flex {
  display: flex;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.space-between {
  justify-content: space-between;
}

.center-horizontal {
  justify-content: center;
}

.text-overflow {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: clip;
  width: 720px;
}

.text-overflow:hover {
  overflow: visible;
}

.img-overflow {
  overflow: scroll;
}

.title {
  font: 42px 'Open Sans', sans-serif;
}

.sub-title {
