.css-button-3d--black {
  min-width: 130px;
  height: 40px;
  color: #fff;
  padding: 5px 10px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  outline: none;
  border-radius: 5px;
  border: none;
  background: #343a40;
  box-shadow: 0 5px #212529;
}
.css-button-3d--black:hover {
  box-shadow: 0 3px #212529;
  top: 1px;
}
.css-button-3d--black:active {
  box-shadow: 0 0 #212529;
  top: 5px;
}

.css-button-3d--grey {
  min-width: 130px;
  height: 40px;
  color: #fff;
  padding: 5px 10px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  outline: none;
  border-radius: 5px;
  border: none;
  background: #6c757d;
  box-shadow: 0 5px #495057;
}
.css-button-3d--grey:hover {
  box-shadow: 0 3px #495057;
  top: 1px;
}
.css-button-3d--grey:active {
  box-shadow: 0 0 #495057;
  top: 5px;
}

.css-button-retro--yellow {
  min-width: 130px;
  height: 40px;
  color: #fff;
  padding: 5px 10px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  outline: none;
  border: 1px solid #000;
  color: #000;
  background: transparent;
}
.css-button-retro--yellow:hover:after {
  top: 0;
  left: 0;
}
.css-button-retro--yellow:after {
  content: '';
  width: 100%;
  z-index: -1;
  position: absolute;
  height: 100%;
  top: 5px;
  left: 5px;
  transition: 0.2s;
  background-color: #ffe566;
}
